  FAQ_MODULE_2: '2. TI KHON CA TI',
  FAQ_QUESTION_2_1: '2.1 To Ti Khon ti HL8?',
  FAQ_ANSWER_2_1: `<div>
        <p> Ch cn nhp vo nt "ng K"  gc trn bn phi mn hnh, v trang ng k cn in thng tin sau:</p>
        <h4> H v Tn</h4>
        <p> Thnh Vin cn nhp h tn chnh xc v trng vi h tn trong ti khon ngn hng v chng t  m bo giao dch gi tin v rt tin c x l trong thi gian nhanh nht.</p>
        <h4> Ngy/ Thng/ Nm Sinh</h4>
        <p> Nhp chnh xc ngy/ thng/ nm sinh  c h tr cho vic xc minh ti khon v nhn thm nhiu iu u i.</p>
        <h4> a Ch Email</h4>
        <p> Nhp chnh xc a ch Email Thnh Vin ang s dng v nhn ngay cc thng tin khuyn mi mi nht, hoc khuyn mi c bit ti HL8.</p>
        <h4> S in Thoi Lin Lc</h4>
        <p> Cung cp s in thoi chnh xc v hp l  c h tr tt nht ng thi m bo an ton cho ti khon v nhn c thng tin ngn hng nhanh nht.</p>
        <h4> Tin T S Dng</h4>
        <p> Thnh Vin cn la chn tin t mun s dng  thc hin giao dch gi/ rt tin v t cc. Nu Thnh Vin s dng tin Vit, vui lng chn VND (Vit Nam ng)</p>
        <h4> Tn ng Nhp</h4>
        <p> Thnh Vin cn nhp ti khon mun s dng khi ng nhp ti HL8 ( di ca tn ng nhp t 6 n 16 k t, khng bao gm cc k t c bit). Tn ti khon/ tn ng nhp khng b trng vi cc ti khon/ tn ng nhp khc.</p>
        <h4> Mt Khu</h4>
        <p> Mt khu bt buc t 6  16 k t, bao gm c ch vit, s v k t c bit.  bo mt thng tin ti khon, Thnh Vin vui lng ghi nh, thay i mt khu thng xuyn v KHNG c tit l thng tin vi bt k ngi no khc.</p>
        <h4> M S i L</h4>
        <p>Nu Thnh Vin c i l gii thiu, nhp m i l vo mc M S i L. Nu khng, c th  trng mc ny. </p>
    </div>`,
  FAQ_QUESTION_2_2: '2.2 Ti C Th To Ti Khon Ti HL8 Khi Cha  18 Tui?',
  FAQ_ANSWER_2_2: `<p>Thnh Vin khng c php tham gia ti HL8 khi di 18 tui. ng k ti khon ti HL8 cn c thm quyn v v mt php l  c v hiu cc iu khon v iu kin.Chng ti s ng/ nh ch ti khon nu b phn kim duyt KYC pht hin ti khon c ng k bi cc c nhn cha  tui.</p>`,
